import React, { Component } from 'react';
import './App.scss';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
import guessCorpus from './assets/words-source-curated5.txt';
import dictionary from './assets/words-dict-curated5.txt'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Help from './Help';
import Stats from './Stats';
import Constants from './Constants'
import Utils from './Utils'
import wordEnterSound from './assets/sounds/word-enter.mp3';
import lostSound from './assets/sounds/lost.mp3';
import wonSound from './assets/sounds/passed.mp3';
import startSound from './assets/sounds/start.mp3'
import errorSound from './assets/sounds/error.mp3';
import Firework from './Firework';
import ResultAd from './ResultAd';

const ATTEMPTS_ALLOWED = 8;

class App extends Component {
  constructor() {
    super()
    this.myContainer = React.createRef()
    this.keyboard = React.createRef()
    this.resultRef = React.createRef()
    this.audios = {
      wordEnter: new Audio(wordEnterSound),
      lost: new Audio(lostSound),
      passed: new Audio(wonSound),
      start: new Audio(startSound),
      error: new Audio(errorSound),
    }
  }
  state = {
    guessCorpus: [],
    dictionary: [],
    guess: false,
    wordLength: 5,
    rowIndex: 0,
    colIndex: 0,
    inputs: [],
    over: false,
    won: false,
    lost: false,
    showHelp: false,
    showStats: false,
    themeMode: '',
    music: true,
    frozen: [],
    showFireWork: false,
    level: 2,
    guessed: [],
    correctedRow: [],
  }

  async play(track) {
    if (this.state.music) {
      track.play()
    }
  }

  async loadWords() {
    fetch(guessCorpus)
      .then(r => r.text())
      .then(text => {
        const words = text.split('\n').filter(word => word.length === this.state.wordLength);
        console.log(`Read ${words.length} words`);

        this.setState({
          guessCorpus: words,
          guesses: this.guessWord(words),
          inputs: this.formInputs(),
          guessed: Array(this.state.level).fill(false),
          correctedRow: Array(this.state.level).fill(ATTEMPTS_ALLOWED)
        }, () => {
          fetch(dictionary)
            .then(r => r.text())
            .then(text => {
              const words = text.split('\n').filter(word => word.length === this.state.wordLength);
              this.setState({
                dictionary: [...words, ...this.state.guessCorpus]
              })
            })
        })
      });

  }


  async initThemeMode() {
    let systemMode = window.matchMedia("(prefers-color-scheme: dark)").matches ? 'dark' : 'light';
    const themeMode = localStorage.getItem(Constants.STORAGE.COLOR_SCHEME) ? localStorage.getItem(Constants.STORAGE.COLOR_SCHEME) : systemMode
    this.setState({
      themeMode: themeMode
    }, () => this.applyThemeMode(themeMode))
  }

  async applyThemeMode(mode) {
    const htmlDom = document.querySelector('html');
    htmlDom.classList.remove('light', 'dark')
    htmlDom.classList.add(mode)
  }

  async toggleThemeMode() {
    this.setState({
      themeMode: this.state.themeMode === 'dark' ? 'light' : 'dark'
    }, () => {
      this.applyThemeMode(this.state.themeMode);
      window.localStorage.setItem(Constants.STORAGE.COLOR_SCHEME, this.state.themeMode);
    })
  }

  initConfig() {
    this.setState({
      music: window.localStorage.getItem(Constants.STORAGE.MUSIC) != 'false'
    })
  }

  appHandling() {
    const _this = this;
    document.addEventListener("deviceready", () => {
      console.log('Device ready inside the react code')
      document.addEventListener("backbutton", (e) => {
        console.log('back button pressed')
        if (_this.state.showHelp || _this.state.showStats) {
          e.preventDefault()
          document.querySelector('body').classList.remove('scroll-lock');
          _this.setState({
            showHelp: false,
            showStats: false,
          })
        }
      }, false);
    }, false);
  }

  componentDidMount() {
    this.loadWords()
    if (!window.localStorage.getItem('help-shown')) {
      this.setState({
        showHelp: true,
      }, () => {
        window.localStorage.setItem('help-shown', true)
      })
    }
    this.initThemeMode()
    this.initConfig()
    this.appHandling();
  }

  guessWord(words) {
    this.play(this.audios.start);
    const guesses = []
    for (let i = 0; i < this.state.level; i++) {
      guesses.push(words[Math.floor(Math.random() * words.length)])
    }
    return guesses;
  }

  formInputs() {
    const inputs = []
    for (let i = 0; i <= ATTEMPTS_ALLOWED; i++) {
      inputs.push(new Array(this.state.wordLength).fill(''))
    }
    return inputs;
  }

  restart() {
    const cells = document.getElementsByClassName('cell');
    for (let cell of cells) {
      cell.classList.remove('correct')
      cell.classList.remove('partial')
      cell.classList.remove('miss')
      cell.removeAttribute('data-state')
      cell.removeAttribute('data-animation')
    }
    this.keyboard.removeButtonTheme('q w e r t y u i o p a s d f g h j k l z x c v b n m', 'partial miss correct')
    this.setState({
      guesses: this.guessWord(this.state.guessCorpus),
      inputs: this.formInputs(),
      rowIndex: 0,
      colIndex: 0,
      over: false,
      won: false,
      showFireWork: false,
      lost: false,
      frozen: [],
      guessed: Array(this.state.level).fill(false),
      correctedRow: Array(this.state.level).fill(ATTEMPTS_ALLOWED)
    })
    this.myContainer.current.scrollIntoView();
  }

  // async updateKeyboardStyle(word, corrects, partials) {
  //   const correctChars = new Set();
  //   const partialChars = new Set();
  //   const missChars = new Set();
  //   for (let i = 0; i < word.length; i++) {
  //     const char = word[i].toLowerCase();
  //     if (corrects.has(i)) {
  //       correctChars.add(char);
  //     } else if (partials.has(i)) {
  //       partialChars.add(char);
  //     } else {
  //       missChars.add(char)
  //     }
  //   }

  //   for (let char of word.toLowerCase()) {
  //     this.keyboard.removeButtonTheme(char, 'partial miss correct')
  //     this.keyboard.button
  //     if (correctChars.has(char)) {
  //       this.keyboard.addButtonTheme(char, 'correct')
  //     } else if (partialChars.has(char)) {
  //       this.keyboard.addButtonTheme(char, 'partial')
  //     } else {
  //       this.keyboard.addButtonTheme(char, 'miss')
  //     }
  //   }
  // }

  async updateStatus(won) {
    if (won) {
      window.localStorage.setItem(Constants.STORAGE.WIN, Number(window.localStorage.getItem(Constants.STORAGE.WIN) || 0) + 1)
      const currentStreak = Number(window.localStorage.getItem(Constants.STORAGE.CURRENT_STREAK) || 0) + 1
      const longestStreak = Number(window.localStorage.getItem(Constants.STORAGE.LONGEST_STREAK) || 0)
      window.localStorage.setItem(Constants.STORAGE.CURRENT_STREAK, currentStreak)
      if (currentStreak > longestStreak) {
        window.localStorage.setItem(Constants.STORAGE.LONGEST_STREAK, currentStreak)
      }
    } else {
      window.localStorage.setItem(Constants.STORAGE.LOST, Number(window.localStorage.getItem(Constants.STORAGE.LOST) || 0) + 1)
      window.localStorage.setItem(Constants.STORAGE.CURRENT_STREAK, 0)
    }
  }

  async checkWord() {

    const word = this.state.inputs[this.state.rowIndex].join('');
    const rowIndex = this.state.rowIndex;
    console.log(this.state.rowIndex, word, '---')
    if (word.length < this.state.wordLength) {
      toast.warn('Word is too short!', {
        position: 'top-center',
        autoClose: 3000,
        onOpen: () => this.play(this.audios.error)
      })
    } else if (this.state.dictionary.find(cand => cand === word)) {
      if (this.state.frozen.indexOf(this.state.rowIndex) != -1) return;
      this.setState({
        frozen: [...this.state.frozen, this.state.rowIndex]
      })
      this.play(this.audios.wordEnter);
      console.log('Word found in the dictionary')

      console.log('check and highilgiht')
      for (let i = 0; i < this.state.level; i++) {
        this.checkForTheWord(word, i, rowIndex);
      }
      this.updateKeyboardStyle(word, rowIndex)
      if (!Utils.cssAnimationAvailable()) {
        this.updateState(word);
      }

    } else {
      console.log('not a dictionary word')
      toast.warn('Not a known word', {
        position: 'top-center',
        autoClose: 3000,
        onOpen: () => this.play(this.audios.error)
      })
    }
  }

  updateKeyboardStyle(word, rowIndex) {
    const keyMap = new Map()
    for (let cInd = 0; cInd < word.length; cInd++) {
      let className = [];
      const char = word[cInd]
      for (let i = 0; i < this.state.level; i++) {
        const guess = this.state.guesses[i]
        if (char === guess[cInd]) {
          className.push('correct')
        } else if (guess.indexOf(char) != -1) {
          className.push('partial')
        } else {
          className.push('miss')
        }
      }
      keyMap.set(char, className.join('-'))
    }
    console.log('Class names are ', keyMap)
    for (let key of keyMap.keys()) {
      this.keyboard.removeButtonTheme(key.toLowerCase(), 'correct-partial correct-miss partial-correct partial-miss miss-correct miss-partial')
      this.keyboard.addButtonTheme(key.toLowerCase(), keyMap.get(key))
    }
  }

  onKeyPress = (button) => {
    console.log("Button pressed", button, this.state);
    if (this.state.over && button !== '{enter}') return;
    if (button === '{backspace}') {
      if (this.state.colIndex > 0) {
        const tmp = this.state.inputs
        tmp[this.state.rowIndex][this.state.colIndex - 1] = ''
        for (let i = 0; i < this.state.level; i++) {
          if (this.state.guessed[i]) continue;
          const cell = document.querySelectorAll(`.input-cells:nth-of-type(${i + 1}) .cell`)[this.state.rowIndex * this.state.wordLength + this.state.colIndex - 1]
          cell.removeAttribute('data-animation', 'pop')
          cell.removeAttribute('data-state', 'tbd')
        }
        this.setState({
          inputs: tmp,
          colIndex: this.state.colIndex - 1
        })
      }
    } else if (button === '{enter}') {
      if (this.state.over) {
        this.restart()
      } else {
        this.checkWord()
      }
    } else {
      button = button.toUpperCase()
      if (this.state.rowIndex < ATTEMPTS_ALLOWED && this.state.colIndex < this.state.wordLength) {
        const tmp = this.state.inputs
        tmp[this.state.rowIndex][this.state.colIndex] = button
        for (let i = 0; i < this.state.level; i++) {
          if (this.state.guessed[i]) continue;
          const cell = document.querySelectorAll(`.input-cells:nth-of-type(${i + 1}) .cell`)[this.state.rowIndex * this.state.wordLength + this.state.colIndex]
          cell.setAttribute('data-animation', 'pop')
          cell.setAttribute('data-state', 'tbd')
        }
        this.setState({
          inputs: tmp,
          colIndex: this.state.colIndex + 1
        })
      }
    }
  }

  checkForTheWord(word, guessIndex, rowIndex) {
    if (this.state.guessed[guessIndex]) return;
    const cells = document.querySelectorAll(`.input-cells:nth-of-type(${guessIndex + 1}) .cell`);
    const row = this.state.rowIndex;
    const cellOffset = this.state.wordLength * row;
    const guess = this.state.guesses[guessIndex];
    const freqDict = (string => string.split('').reduce((freqDict, char) => {
      freqDict[char] = (freqDict[char] || 0) + 1;
      return freqDict;
    }, {}))(guess);
    console.log(freqDict);
    const corrects = new Set();
    const partials = new Set();
    for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
      const char = word[cInd];
      if (guess[cInd] === char) {
        corrects.add(cInd);
        freqDict[char] -= 1;
      }
    }
    console.log('corrects', corrects);
    for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
      const char = word[cInd];
      if (guess.indexOf(char) !== -1 && !corrects.has(cInd) && freqDict[char] > 0) {
        partials.add(cInd);
        freqDict[char] -= 1;
      }
    }
    console.log('partials', partials);
    this.updateCharStyle(word, cells, cellOffset, corrects, partials, guessIndex, rowIndex);
    this.updateKeyboardStyle(word, corrects, partials, guessIndex);
  }

  updateState(word, guessIndex, rowIndex) {
    const guess = this.state.guesses[guessIndex]
    const guessed = this.state.guessed.slice(0)
    if (guess === word) {
      const correctedRows = this.state.correctedRow.slice(0)
      correctedRows[guessIndex] = rowIndex
      guessed[guessIndex] = true;
      this.setState({
        guessed: guessed,
        correctedRow: correctedRows,
      }, () => {
        console.log('State after guessing one word', this.state)
      })
    }
    if (guessed.every(Boolean)) {
      this.play(this.audios.passed)
      this.setState({
        over: true,
        won: true
      }, () => {
          this.resultRef.current.scrollIntoView({
            block: 'center'
          });
          this.resultRef.current.addEventListener("animationend", () => {
            console.log('fireworks', this.state.showFireWork)
            this.setState({
              showFireWork: true,
            }, () => {
              console.log(this.state.showFireWork)
            })
          }, {
            once: true,
          })
        });
      this.updateStatus(true);
    } else if (this.state.rowIndex < ATTEMPTS_ALLOWED) {
      if (rowIndex === this.state.rowIndex) {
        this.setState({
          rowIndex: this.state.rowIndex + 1,
          colIndex: 0,
        });
      }
    } else {
      this.play(this.audios.lost)
      this.setState({
        over: true,
        lost: true
      }, () => this.resultRef.current.scrollIntoView({
        block: 'center'
      }));
      this.updateStatus(false);
    }
  }

  updateCharStyle(word, cells, cellOffset, corrects, partials, guessIndex, rowIndex) {
    if (Utils.cssAnimationAvailable()) {
      const _this = this;
      animateChar(0);

      function animateChar(cInd) {
        const cell = cells[cellOffset + cInd];
        cell.setAttribute('data-animation', 'flip-out');
        cell.addEventListener("animationend", () => {
          if (corrects.has(cInd)) {
            cell.classList.add('correct');
          } else if (partials.has(cInd)) {
            cell.classList.add('partial');
          } else {
            cell.classList.add('miss');
          }
          cell.setAttribute('data-animation', 'flip-in');
          cell.addEventListener("animationend", () => {
            if (cInd < _this.state.wordLength - 1) {
              animateChar(cInd + 1)
            } else {
              _this.updateState.call(_this, word, guessIndex, rowIndex);
            }
          }, {
            once: true
          })
        }, {
          once: true
        });
      }
    } else {
      for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
        const cell = cells[cellOffset + cInd];
        if (corrects.has(cInd)) {
          cell.classList.add('correct');
        } else if (partials.has(cInd)) {
          cell.classList.add('partial');
        } else {
          cell.classList.add('miss');
        }
      }
    }
  }

  get renderResultBlocks() {
    let result = ''

    for (let level = 0; level < this.state.level; level++) {
      const guess = this.state.guesses[level];
      for (let i = 0; i < this.state.inputs.length; i++) {
        const word = this.state.inputs[i].join('')
        if (!word.length) break;
        for (let j = 0; j < word.length; j++) {
          if (word[j] === guess[j]) {
            result += ''
          } else if (guess.indexOf(word[j]) !== -1) {
            result += ''
          } else {
            result += this.state.themeMode === 'dark' ? '' : ''
          }
        }
        result += '\n'
        if (i === this.state.correctedRow[level]) {
          break;
        }
      }
      result += '\n'
    }
    return (
      <div className='btn btn-primary' onClick={() => {
        const title = "Dordle Unlimited"
        const message = `*Dordle Unlimited*\n\nI have guessed the words in Dordle Unlimited.\n${result}\n\nDo you want to try?\nAndroid: https://play.google.com/store/apps/details?id=org.tahrik.wordle\n\nWeb: `
        const url = "https://opensourcefeed.org/puzzles/dordle-unlimited/?source=share";
        if (window.navigator.share) {
          window.navigator.share({
            text: message,
            url: url,
            title: title,
          })
        } else {
          navigator.clipboard.writeText(`${message}${url}`)
          toast('Content has been copied to clipboard. You can paste and share now')
        }
      }}>Share</div>)
  }

  get renderCells() {
    const windowWidth = window.innerWidth < 480 ? window.innerWidth : 480
    const offsetWidth = (windowWidth) - this.state.wordLength * 30;
    const indices = []
    for (let i = 0; i < ATTEMPTS_ALLOWED; i++) {
      indices.push(i)
    }
    return <div className='panel'>{[...Array(this.state.level).keys()].map(levelIndex => <table key={levelIndex} className='table input-cells'><tbody>
      {this.state.inputs.length ? indices.map(index => <tr key={index}>{indices.slice(0, this.state.wordLength).map(cInd => <td key={cInd}>
        <div className='cell' defaultValue={''} style={{
          height: offsetWidth / (this.state.wordLength * this.state.level),
          width: offsetWidth / (this.state.wordLength * this.state.level),
          fontSize: offsetWidth / (this.state.wordLength * this.state.level) - 4
        }}>{index <= this.state.correctedRow[levelIndex] ? this.state.inputs[index][cInd] : ''}</div></td>)}</tr>) : null}
    </tbody>
    </table>)}</div>
  }

  get randomColor() {
    const colors = ['red', 'yellow', 'white', 'orange']
    return colors[Math.floor(Math.random() * colors.length)]
  }

  get renderHeader() {
    return (<header>
      <table className='header-table'>
        <tbody><tr>
          <td style={{ textAlign: 'center' }}><span onClick={() => {
            this.setState({
              showHelp: true
            })
            document.querySelector('body').classList.add('scroll-lock');
          }} className='icon'>
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 17h-2v-2h2v2zm2.07-7.75l-.9.92c-.5.51-.86.97-1.04 1.69-.08.32-.13.68-.13 1.14h-2v-.5c0-.46.08-.9.22-1.31.2-.58.53-1.1.95-1.52l1.24-1.26c.46-.44.68-1.1.55-1.8-.13-.72-.69-1.33-1.39-1.53-1.11-.31-2.14.32-2.47 1.27-.12.37-.43.65-.82.65h-.3C8.4 9 8 8.44 8.16 7.88c.43-1.47 1.68-2.59 3.23-2.83 1.52-.24 2.97.55 3.87 1.8 1.18 1.63.83 3.38-.19 4.4z"/></svg>
          </span></td>

          <td onClick={() => {
            this.setState({
              music: !this.state.music
            }, () => {
              window.localStorage.setItem(Constants.STORAGE.MUSIC, this.state.music);
            })
            
          }}><span className='icon'>
            {!this.state.music ? <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M3.63 3.63c-.39.39-.39 1.02 0 1.41L7.29 8.7 7 9H4c-.55 0-1 .45-1 1v4c0 .55.45 1 1 1h3l3.29 3.29c.63.63 1.71.18 1.71-.71v-4.17l4.18 4.18c-.49.37-1.02.68-1.6.91-.36.15-.58.53-.58.92 0 .72.73 1.18 1.39.91.8-.33 1.55-.77 2.22-1.31l1.34 1.34c.39.39 1.02.39 1.41 0 .39-.39.39-1.02 0-1.41L5.05 3.63c-.39-.39-1.02-.39-1.42 0zM19 12c0 .82-.15 1.61-.41 2.34l1.53 1.53c.56-1.17.88-2.48.88-3.87 0-3.83-2.4-7.11-5.78-8.4-.59-.23-1.22.23-1.22.86v.19c0 .38.25.71.61.85C17.18 6.54 19 9.06 19 12zm-8.71-6.29l-.17.17L12 7.76V6.41c0-.89-1.08-1.33-1.71-.7zM16.5 12c0-1.77-1.02-3.29-2.5-4.03v1.79l2.48 2.48c.01-.08.02-.16.02-.24z"/></svg> : <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M3 10v4c0 .55.45 1 1 1h3l3.29 3.29c.63.63 1.71.18 1.71-.71V6.41c0-.89-1.08-1.34-1.71-.71L7 9H4c-.55 0-1 .45-1 1zm13.5 2c0-1.77-1.02-3.29-2.5-4.03v8.05c1.48-.73 2.5-2.25 2.5-4.02zM14 4.45v.2c0 .38.25.71.6.85C17.18 6.53 19 9.06 19 12s-1.82 5.47-4.4 6.5c-.36.14-.6.47-.6.85v.2c0 .63.63 1.07 1.21.85C18.6 19.11 21 15.84 21 12s-2.4-7.11-5.79-8.4c-.58-.23-1.21.22-1.21.85z"/></svg>}
          </span></td>
          <td className='title-wrapper'><h1 className="title">Dordle Unlimited</h1></td>
          <td><span className='icon' onClick={() => {
            this.setState({
              showStats: true
            })
            document.querySelector('body').classList.add('scroll-lock');
          }}>
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zM8 17c-.55 0-1-.45-1-1v-5c0-.55.45-1 1-1s1 .45 1 1v5c0 .55-.45 1-1 1zm4 0c-.55 0-1-.45-1-1V8c0-.55.45-1 1-1s1 .45 1 1v8c0 .55-.45 1-1 1zm4 0c-.55 0-1-.45-1-1v-2c0-.55.45-1 1-1s1 .45 1 1v2c0 .55-.45 1-1 1z" /></svg>
          </span></td>
          <td><span className="icon" onClick={this.toggleThemeMode.bind(this)}>
            {this.state.themeMode === 'dark' && <svg xmlns="http://www.w3.org/2000/svg" enableBackground="new 0 0 24 24" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><rect fill="none" height="24" width="24" /><path d="M12,7c-2.76,0-5,2.24-5,5s2.24,5,5,5s5-2.24,5-5S14.76,7,12,7L12,7z M2,13l2,0c0.55,0,1-0.45,1-1s-0.45-1-1-1l-2,0 c-0.55,0-1,0.45-1,1S1.45,13,2,13z M20,13l2,0c0.55,0,1-0.45,1-1s-0.45-1-1-1l-2,0c-0.55,0-1,0.45-1,1S19.45,13,20,13z M11,2v2 c0,0.55,0.45,1,1,1s1-0.45,1-1V2c0-0.55-0.45-1-1-1S11,1.45,11,2z M11,20v2c0,0.55,0.45,1,1,1s1-0.45,1-1v-2c0-0.55-0.45-1-1-1 C11.45,19,11,19.45,11,20z M5.99,4.58c-0.39-0.39-1.03-0.39-1.41,0c-0.39,0.39-0.39,1.03,0,1.41l1.06,1.06 c0.39,0.39,1.03,0.39,1.41,0s0.39-1.03,0-1.41L5.99,4.58z M18.36,16.95c-0.39-0.39-1.03-0.39-1.41,0c-0.39,0.39-0.39,1.03,0,1.41 l1.06,1.06c0.39,0.39,1.03,0.39,1.41,0c0.39-0.39,0.39-1.03,0-1.41L18.36,16.95z M19.42,5.99c0.39-0.39,0.39-1.03,0-1.41 c-0.39-0.39-1.03-0.39-1.41,0l-1.06,1.06c-0.39,0.39-0.39,1.03,0,1.41s1.03,0.39,1.41,0L19.42,5.99z M7.05,18.36 c0.39-0.39,0.39-1.03,0-1.41c-0.39-0.39-1.03-0.39-1.41,0l-1.06,1.06c-0.39,0.39-0.39,1.03,0,1.41s1.03,0.39,1.41,0L7.05,18.36z" /></svg>}
            {this.state.themeMode === 'light' && <svg xmlns="http://www.w3.org/2000/svg" enableBackground="new 0 0 24 24" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><rect fill="none" height="24" width="24" /><path d="M11.01,3.05C6.51,3.54,3,7.36,3,12c0,4.97,4.03,9,9,9c4.63,0,8.45-3.5,8.95-8c0.09-0.79-0.78-1.42-1.54-0.95 c-0.84,0.54-1.84,0.85-2.91,0.85c-2.98,0-5.4-2.42-5.4-5.4c0-1.06,0.31-2.06,0.84-2.89C12.39,3.94,11.9,2.98,11.01,3.05z" /></svg>}
          </span></td>
        </tr>
        </tbody>
      </table>
    </header>)
  }

  render() {
    return (
      <div className="App" ref={this.myContainer}>
        {this.renderHeader}
        {/* I have read {this.state.words.length} words, so far!!.
        {this.state.guess && <div>I guess the word {this.state.guess}</div>} */}
        {this.renderCells}
        {this.renderResult()}
        <div className='keys' style={{ visibility: this.state.showHelp ? 'hidden' : 'visible' }}>
          <Keyboard
            keyboardRef={r => this.keyboard = r}
            onKeyPress={this.onKeyPress.bind(this)}
            layoutName='default'
            layout={{
              default: [
                "q w e r t y u i o p",
                "a s d f g h j k l",
                "{enter} z x c v b n m {backspace}"
              ]
            }}
            display={{
              '{enter}': 'SUBMIT',
              '{backspace}': '&lArr;',
              'a': 'A',
              'b': 'B',
              'c': 'C',
              'd': 'D',
              'e': 'E',
              'f': 'F',
              'g': 'G',
              'h': 'H',
              'i': 'I',
              'j': 'J',
              'k': 'K',
              'l': 'L',
              'm': 'M',
              'n': 'N',
              'o': 'O',
              'p': 'P',
              'q': 'Q',
              'r': 'R',
              's': 'S',
              't': 'T',
              'u': 'U',
              'v': 'V',
              'w': 'W',
              'x': 'X',
              'y': 'Y',
              'z': 'Z'
            }}
            physicalKeyboardHighlight={true}
            physicalKeyboardHighlightPress={true}
          />
        </div>
        <ToastContainer />
        {this.state.showHelp && <Help secret={this.state.guesses.join(', ')} onCloseRequest={() => {
          this.setState({
            showHelp: false
          })
          document.querySelector('body').classList.remove('scroll-lock');
        }} />}
        {this.state.showStats && <Stats secret={this.state.guess} onCloseRequest={() => {
          this.setState({
            showStats: false
          })
          document.querySelector('body').classList.remove('scroll-lock');
        }} />}
      </div>
    );
  }

  restartButton() {
    return (<div className='btn btn-pinky' onClick={() => this.restart()}>
      Restart
    </div>)

  }

  resultAd() {
    return <ResultAd/>
  }

  renderResult() {
    return <div ref={this.resultRef}>
      {this.state.won && <div data-animation='fade-in' className='message message-success'>
        <strong>Brilliant </strong><br />
        You have found the words<br /><br/>
        {this.renderResultBlocks}&nbsp;&nbsp;
        {this.restartButton()}
        {this.resultAd()}
        {this.state.showFireWork && <Firework key={new Date().getTime()}/>}
      </div>}
      {this.state.lost && <div data-animation='fade-in' className='message message-failure'><strong>You Lost! </strong><br /> The words were <strong>{this.state.guesses.join(' ,')}</strong>.<br />
      <br/>
      {this.restartButton()}<br/>
      {this.resultAd()}
      </div>}
    </div>;
  }
}

export default App;
